// lib
import React, { useEffect, useContext } from "react";
import Skeleton from "react-loading-skeleton";

import "react-loading-skeleton/dist/skeleton.css";
// local
import HeaderSubMenu from "../../components/HeaderSubMenu";
import AboutContext from "../../context/AboutProvider";

const PrivacyPolicy = () => {
  // context
  const { privacy, contentPrivacy, loading } = useContext(AboutContext);
  useEffect(() => {
    privacy();
  }, []);

  return (
    <>
      <HeaderSubMenu name={"Privacy Policy"} />
      {loading ? (
        <div className="mx-6">
          <Skeleton height={30} />
        </div>
      ) : (
        <p className="mx-6 mt-6 text-justify">{contentPrivacy}</p>
      )}
    </>
  );
};

export default PrivacyPolicy;
